@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,700");
.App {
  width: 100vw;
  height: 100vh;
  background: #fff;
  color: #212121;
  font-family: "Open Sans", sans-serif;
  display: grid;
  place-items: center;
  position: relative;
}
body {
  margin: 0;
  padding: 0;
}

.floating-image {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
  height: 150px;
  width: 150px;
  cursor: pointer;
}