.joinChatContainer {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.joinChatContainer h3 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
}
.joinChatContainer input {
    width: 210px;
    height: 40px;
    margin: 7px;
    border: 2px solid #43a047;
    border-radius: 5px;
    padding: 5px;
    font-size: 16px;
}

.joinChatContainer button {
    width: 225px;
    height: 50px;
    margin: 7px;
    border: none;
    border-radius: 5px;
    padding: 5px;
    font-size: 16px;
    background: #43a047;
    color: #fff;
    cursor: pointer;
}

.joinChatContainer button:hover {
    background: #2e7d32;
}

.chat-window {
    width: 300px;
    height: 420px;
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1000;
}

.chat-window p {
    margin: 0;
}
.chat-window .chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 45px;
    border-radius: 6px;
    background: #482d58;
    position: relative;
    cursor: pointer;
    padding: 0 10px;
}
.chat-window .chat-header p {
    display: block;
    padding: 0 1em 0 2em;
    color: #fff;
    font-weight: 700;
    line-height: 45px;
}

.chat-window .chat-body {
    height: calc(450px - (45px + 70px));
    border: 1px solid #becbc6;
    background: #e8f1ed;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    position: relative;
}
.chat-window .chat-body .message-container {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
    display: none;
}
.chat-window .chat-body .message {
    height: auto;
    display: flex;
}

.chat-window .chat-body .message .message-content {
    width: auto;
    height: auto;
    min-height: 40px;
    max-width: 150px;
    background-color: #43a047;
    border-radius: 5px;
    color: white !important;
    display: flex;
    align-items: center;
    margin-right: 5px;
    margin-left: 5px;
    padding-right: 5px;
    padding-left: 5px;
    overflow-wrap: break-word;
    word-break: break-word;
}

#you {
    justify-content: flex-start;
}

#you .message-content {
    justify-content: flex-start;
}

#you .message-meta {
    justify-content: flex-start;
    margin-left: 5px;
}

#other {
    justify-content: flex-end;
}

#other .message-content {
    justify-content: flex-end;
    background-color: #0f6bc3;
    padding: 3px 10px;
}

#other .message-meta {
    justify-content: flex-end;
    margin-right: 5px;
}

.message-meta #author {
    margin-left: 10px;
    font-weight: bold;
}

.chat-window .chat-body .message .message-meta {
    display: flex;
    font-size: 12px;
}

.chat-window .chat-footer {
    height: 40px;
    border: 1px solid #becbc6;
    border-top: none;
    display: flex;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: #ebf3ef;
}

.chat-window .chat-footer input {
    height: 100%;
    flex: 85%;
    border: 0;
    padding: 0 0.7em;
    font-size: 1em;
    border-right: 1px dotted #607d8b;
    background-color: #d3dcd6;
    outline: none;
    font-family: "Open Sans", sans-serif;
}

.chat-window .chat-footer button {
    border: 0;
    display: grid;
    place-items: center;
    cursor: pointer;
    flex: 15%;
    height: 100%;
    background: transparent;
    outline: none;
    font-size: 25px;
    color: seagreen;
}

.chat-window .chat-footer button:hover {
    color: #43a047;
}
.hide {
    opacity: 0 !important;
}

.cancel-icon {
    height: 20px;
    width: 20px;
    cursor: pointer;
    /*background-color: white;*/
}

.invisible {
    visibility: hidden;
}

.message-text {
    color: white;
    padding: 5px;
}