.smart-home-assistant {
    font-family: Arial, sans-serif;
    margin: 20px;
}

.section {
    margin-bottom: 30px;
}

h2 {
    color: #333;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
}

h3 {
    color: #666;
    font-size: 18px;
    margin-bottom: 10px;
}

h4 {
    color: #888;
    font-size: 16px;
    margin-bottom: 10px;
}

p {
    color: #333;
    margin-bottom: 10px;
}

ul, ol {
    color: #333;
    margin-bottom: 10px;
    padding-left: 20px;
}

strong {
    font-weight: bold;
}
